import { OnInit, Component, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';

/**
 * Generated class for the LightsChartComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'lights-chart',
  templateUrl: 'lights-chart.html'
})
export class LightsChartComponent implements OnInit{
  @ViewChild('barCanvas') barCanvas;
  //@Input() colorArr : Array<number>;
  //@Input() scheduleData : Array<number>;

  barChart: any;

  constructor() {
    console.log('LightsChartComponent');

  }
  ngOnInit() {
    console.log('this.barCanvas');
    this.generateChart();
    console.log(this.barCanvas);
    
  }
  generateChart() {
    let ctx = document.getElementById("myChart");
    this.barChart = new Chart(ctx, {
      type: 'bar',
      data: {
        labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
        datasets: [{
          label: '# of Votes',
          data: [12, 19, 3, 5, 2, 3],
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)'
          ],
          borderColor: [
            'rgba(255,99,132,1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)'
          ],
          borderWidth: 1
        }]
      },
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }]
        }
      }

    });
  }

}



